import { Button, Card, Table } from "@themesberg/react-bootstrap";
import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { securedAxiosInstance } from "../../axiosConfig";
import Loader from "../../components/Loader";
import { AuthContext } from "../../context/AuthContext";

const TableRow = (props) => {
  const history = useHistory();

  const { jobtitle, company, id, status } = props?.company_event || {};
  const [companyName] = useState(props?.company_event?.company?.Name || "No Company");

  return (
    <tr style={{ verticalAlign: "middle" }}>
      <td className="fw-bold border-0" style={{ width: "40%" }}>
        {jobtitle ? jobtitle : "-"}
      </td>
      <td className="fw-bold border-0" style={{ width: "30%" }}>
        {company ? companyName : "-"}
      </td>
      <td className="fw-bold border-0" style={{ width: "20%" }}>
        {status ? status : "-"}
      </td>
      {/* <td className="fw-bold border-0">{details ? details : "-"}</td> */}
      <td className="fw-bold border-0" style={{ width: "10%" }}>
        <Button
          variant="primary"
          style={{
            paddingLeft: "15px",
            paddingRight: "15px",
            paddingTop: "5px",
            paddingBottom: "5px",
            fontSize: "14px",
            background: "linear-gradient(to right, #fb7b4a, #ee3344)",
            borderColor: "#fb7b4a",
          }}
          onClick={() => history.push(`/companies/${id}`)}
        >
          SEE MORE
        </Button>
      </td>
    </tr>
  );
};

const Application = () => {
  const { state } = useContext(AuthContext);
  const [myApplications, setMyApplications] = useState(undefined);

  useEffect(() => {
    if (state.userDetails) {
      securedAxiosInstance
        .get(`/students/my_applied/`)
        .then((response) => {
          setMyApplications(response.data);
        })
        .catch((error) => {
          console.log(error, error.response);
        });
    }
  }, [state]);

  if (!myApplications) return <Loader />;

  return (
    <div>
      <div>
        <h1>My Applications</h1>
      </div>
      <Card border="light" className="shadow-sm">
        <Card.Body className="pb-0">
          <Table
            responsive
            className="table-centered table-nowrap rounded mb-0"
          >
            <thead className="thead-light">
              <tr>
                <th className="border-0">Job Post</th>
                <th className="border-0">Company</th>
                <th className="border-0">Status</th>
                <th className="border-0"></th>
              </tr>
            </thead>
            <tbody>
              {myApplications.length > 0 ? (
                myApplications.map((r) => (
                  <TableRow key={`ranking-${r.id}`} {...r} />
                ))
              ) : (
                <tr style={{ verticalAlign: "middle" }}>
                  <td className="fw-bold border-0" style={{ width: "40%" }}>
                    You have no active applications.
                  </td>
                </tr>
              )}
            </tbody>
          </Table>
        </Card.Body>
      </Card>
    </div>
  );
};

export default Application;
